import './progress.component.styl';

import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';

@Component({
  selector: 'sa-progress',
  template: require('./progress.component.html')
})
export class ProgressComponent implements OnInit, OnChanges {

  public barClass: string;
  public barWidth: string;
  public barHeight: string;

  @Input()
  public align: string = ''; // ['', 'right', 'bottom']

  @Input()
  public vertical: boolean = false; // 

  @Input()
  public striped: boolean = false; // 

  @Input()
  public active: boolean = false; // 

  @Input()
  public size: string = ''; // ['micro', 'xs', 'sm', '', 'lg']

  @Input()
  public maxValue: number = 100; // 

  @Input()
  public type: string = ''; // ['danger', 'warning', 'success', 'info', 'primary']

  @Input()
  public class: string = ''; // class

  @Input()
  public value: number; // value

  @Input()
  public wideBar: boolean = false; // 

  @Input()
  public bgColorClass: string = ''; // 

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.value || changes.maxValue) {
      this.calcBarWidthOrHeight();
    }
    this.calcBarClass();
  }

  private calcBarWidthOrHeight(): void {
    let per = this.value / this.maxValue * 100;
    if (per !== per) { // Is NaN
      this.barWidth = null;
      this.barHeight = null;
      return;
    }
    if (per > 100) {
      per = 100;
    }
    if (this.vertical) {
      this.barHeight = `${per}%`;
    } else {
      this.barWidth = `${per}%`;
    }
  }

  private calcBarClass() {
    let classArr = [];
    if (this.type) {
      classArr.push(`progress-bar-${this.type}`);
    }
    if (this.size) {
      classArr.push(`progress-${this.size}`);
    }
    if (this.align) {
      classArr.push(this.align);
    }
    if (this.vertical) {
      classArr.push('vertical');
    }
    if (this.striped) {
      classArr.push('progress-striped');
    }
    if (this.active) {
      classArr.push('active');
    }
    if (this.class) {
      classArr.push(this.class);
    }
    if (this.wideBar) {
      classArr.push('wide-bar');
    }
    this.barClass = classArr.join(' ');
  }
}



